.footer {
  height: 30%;
  background-color: #20232a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .items {
    width: 200px;
    height: 60px;
    border: 1px solid;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    .inst {
      .instImg {
        width: 50px;
        height: 50px;
      }
    }
    .linkedIn {
      .linkedInImg {
        width: 50px;
        height: 50px;
      }
    }
    .tele {
      .teleImg {
        width: 50px;
        height: 50px;
      }
    }
  }
}
